<template>
  <div class="row">
    <h2>
      Welcome to the Mail-Sender App
    </h2>
    <form class="form-horizontal margin-form" v-on:submit.prevent="submit">
      <div class="form-group">
        <label class="control-label col-sm-2" for="email">Email:</label>
        <div class="col-sm-10">
          <input v-model="user.email" type="email" class="form-control" id="email" placeholder="Enter email">
        </div>
      </div>
      <div class="form-group">
        <label class="control-label col-sm-2" for="pwd">Password:</label>
        <div class="col-sm-10">
          <input pattern="[a-zA-Z0-9!@#$%^*_|]{6,25}" v-model="user.password" type="password" class="form-control" id="pwd" placeholder="Enter password">
        </div>
      </div>
      <div v-show="error" class="row text-center">
        <span class="error"> {{error}} </span>
      </div>
      <div class="form-group">
        <div>
          <button type="submit" class="btn btn-lg btn-primary">Sign In</button>
        </div>
      </div>
    </form>
    <router-link to="sign-up">Have not an account?</router-link>
  </div>
</template>

<script>
import Config from '../config';
import _ from 'lodash';
export default {
  name: 'SignIn',
  data() {
    return {
      user: {
        password: undefined,
        email: undefined
      },
      error: false
    }
  },
  methods: {
    submit() {
      this.$http
        .post(`${Config.baseUrl}authorize`, this.user)
        .then((response) => {
          this.$cookie.set('access_token', _.get(response, 'body.access_token'), { expires: 7, domain: window.location.hostname });
          this.$localStorage.set('GlobalUser', JSON.stringify(response.body));
          return this.$router.push('/home');
        })
        .catch(error => {
          console.log('error', error);
          this.error = error.body.error_description ? error.body.error_description : 'There are some errors';


        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.error {
  color: red;
  font-size: 16px
}

h1,
h2 {
  font-weight: normal;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: #42b983;
}
</style>



