<template>
    <div class="row">
        <div class="row">
            <div class="form-group pull-right  margin-form">
                <div>
                    <button @click="logout" class="btn btn-lg btn-primary">Logout</button>
                </div>
            </div>
        </div>
        <div class="row">
            <form class="form-horizontal margin-form" v-on:submit.prevent="submit">
                <h2>
                    Hello &nbsp;{{GlobalUser&&GlobalUser.email}}!
                </h2>
                <div class="form-group">
                    <label class="control-label " for="email">Email:</label>
                    <input v-model="newMessage.email" type="email" class="form-control" id="email" placeholder="Enter email" required>
                </div>
                <div class="form-group">
                    <label for="comment">Message Text:</label>
                    <textarea v-model="newMessage.message_body" class="form-control" rows="5" id="comment" placeholder="Enter Message Text"></textarea required>
                </div>
    
                <div class="form-group">
                    <div>
                        <button type="submit" class="btn btn-lg btn-primary">Send Message</button>
                    </div>
                </div>
            </form>
        </div>
        <div class="row margin-form">
            <h2>
                Most Popular Tokens
            </h2>
            <table class="table">
                <thead>
                    <tr>
                        <th>Token Key</th>
                        <th>Count</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="data in tableData" v-bind:key="data.key">
                        <td>{{data.key}}</td>
                        <td>{{data.count}}</td>
                    </tr>
    
                </tbody>
            </table>
        </div>
        <div class="row margin-form">
            <h2>
                Total Messages {{UserStats.stats.totalMessages}}
            </h2>
            <table class="table">
                <thead>
                    <tr>
                        <th>Message Id</th>
                        <th>Email</th>
                        <th>Message Body</th>
                        <th>Created At</th>
                        <th>Status</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="data in messageData" v-bind:key="data.id">
                        <td>{{data.id}}</td>
                        <td>{{data.email}}</td>
                        <td>{{data.message_body}}</td>
                        <td>{{data.created_at}}</td>
                        <td>{{data.status}}</td>
                    </tr>
    
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import Config from '../config';

export default {
    name: 'Home',
    data() {
        return {
            newMessage: {
                message_body: undefined,
                email: undefined
            },
            error: false,
            tableData: [],
            messageData: [],
            UserStats: {
                stats: {}
            }

        }
    },
    created() {
        this.$http
            .get(`${Config.apiUrl}users/${this.GlobalUser.id}/stats`)
            .then((response) => {
                console.log('SUCCESS');
                this.UserStats = response.body;
                this.tableData = response.body.stats ? response.body.stats.mostPopularTokens ? response.body.stats.mostPopularTokens : [] : [];
                this.messageData = response.body.stats ? response.body.stats.lastMessages ? response.body.stats.lastMessages : [] : [];
            })
            .catch(error => {
                console.log('error', error);
                this.error = error.body.error_description ? error.body.error_description : 'There are some errors';


            });
    },
    methods: {
        submit() {
            this.$http
                .post(`${Config.apiUrl}messages`, this.newMessage)
                .then((response) => {
                    console.log('SUCCESS');
                    this.newMessage = {};
                })
                .catch(error => {
                    console.log('error', error);
                    this.error = error.body.error_description ? error.body.error_description : 'There are some errors';


                });
        },
        logout() {
            this.$localStorage.remove('GlobalUser');
            this.$cookie.delete('access_token');
            return this.$router.push('/sign-in');
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}

.margin-form {
    margin-right: 20%;
    margin-left: 20%
}
</style>



