// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import $ from 'jquery';
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
// import 'jquery';
import BootstrapVue from 'bootstrap-vue';
import Resource from 'vue-resource'
import Config from './config';
import VueLocalStorage from 'vue-localstorage'

Vue.use(VueLocalStorage);
Vue.use(BootstrapVue);
Vue.config.productionTip = false;

router.beforeEach((route, redirect, next) => {

  function handleAuth(token) {

    if (token != null && token != '')
      return next();
    else
      return next('/sign-in');
  }
  if (route.matched.some(record => record.meta.auth)) {
    var token = router.app.$cookie.get('access_token');
    return handleAuth(token);
  } else {
    return next();
  }
})
Vue.mixin({
  data: function () {
    return {
      get GlobalUser() {
        return JSON.parse(Vue.localStorage.get('GlobalUser'));
      },
    }
  }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  beforeCreate() {
    this.$http
      .post(`${Config.baseUrl}authorize/token`, { token: this.$cookie.get('access_token') })
      .then(response => {
        Vue.localStorage.remove('GlobalUser');
        Vue.localStorage.set('GlobalUser', JSON.stringify(response.body));
        return;
      })
      .catch(err => {
        Vue.localStorage.remove('GlobalUser');
        this.$cookie.delete('access_token');
        return this.$router.push('/sign-in');
      })
  },

})



